.project {
  list-style: none;
  padding: 0 0 30px;

  .project-title {
    padding-bottom: 4px;
  }

  .project-description {
    padding-bottom: 4px;
  }

  .project-tags {
    padding-bottom: 10px;
    word-wrap: break-word;
  }

  .project-tag-inline {
    margin-right: 3px;
  }

  .project-link-holder {
    display: flex;
    justify-content: space-between;
   }
}

@include media-up(sm) {
  .project .project-link-holder {
    justify-content: flex-start;
  }
}
